import React,{useEffect,useState} from 'react';

const Grid3 = (props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>3)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>3)throw new Error("Array Index of Exception");
            if(props.title!=null)if(props.title.length>4)throw new Error("Array Index of Exception");
            
            setProp({
                backgrounds : props.backgrounds||["red","red","red"],
                images : props.images||[img,"",""],
                title : props.title||["title","title","title","title"]
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <>
        <div className="columns is-multiline" style={{textAlign:"center",height:"550px",background:prop.backgrounds[0],marginLeft:"-1.5rem",marginRight:"-1.5rem"}}>
        <div class="column is-full">{props.children}</div>
        <div className="column" style={{position:"relative"}}>
            <h1>{prop.title[0]}</h1>
            <img style={{height:"calc(376px)"}} src={prop.images[0]} alt=""/>
        </div>
        <div className="column">
                <div className="columns is-multiline">
                    <div className="column is-full">
                        <p style={{background:prop.backgrounds[1],height:"calc(176px)"}} class="middle">
                            <img src={prop.images[1]} alt={prop.title[0]}/>
                            <p>{prop.title[0]}</p>
                        </p>
                    </div>
                    <div className="column is-full">
                        <p style={{background:prop.backgrounds[2],height:"calc(176px)"}} class="middle">
                            <img src={prop.images[2]} alt={prop.title[1]}/>
                            <p>{prop.title[1]}</p>
                        </p>
                    </div>
                </div>
        </div>
        </div>
        </>
    );
}
export default Grid3;